import {
	mapState,
	mapActions,
	mapGetters
} from 'vuex'
import commonnav from '../../components/common/commonnav'
import cartsmodel from '../../components/carts/cartsmodel'
import uniSwipeAction from '../../components/uni-swipe-action/uni-swipe-action.vue'
import uniSwipeActionItem from '../../components/uni-swipe-action-item/uni-swipe-action-item.vue'
export default {
	name: "",
	components: {
		commonnav,
		cartsmodel,
		uniSwipeAction,
		uniSwipeActionItem
	},
	props: {},
	data() {
		return {
			openId: '',
			options: [{
				text: '删除',
				style: {
					backgroundColor: '#B4282D'
				}
			}],
			// 选中的数组
			chooseArr: []
		}
	},
	methods: {
		...mapActions(['getCartsNum', 'delCartsGoods']),
		// 跳转到首页
		goToIndex() {
			uni.switchTab({
				url: '../index/index'
			})
		},
		// 单选框事件
		changeChecked(goodsindex) {
			this.newCartsArr[goodsindex].checked = !this.newCartsArr[goodsindex].checked
			this.chooseArr = this.newCartsArr.filter(item => {
				return item.checked === true
			})
		},
		// 全选
		chooseAll() {
			if (this.allFlag) {
				this.chooseArr = []
				this.newCartsArr.map(item => {
					item.checked = false
				})
			} else {
				this.chooseArr = [];
				this.newCartsArr.map(item => {
					item.checked = true
					this.chooseArr.push(item);
				})
			}
		},
		// 删除按钮
		onClick(indexs) {
			uni.showModal({
				title: '提醒',
				content: '您确定将该商品从您的购物车中删除吗?',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						uni.showToast({
							title: '删除成功'
						});
						this.delCartsGoods(this.newCartsArr[indexs].id)
						this.chooseArr = []
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 结算按钮
		goToSettlement() {
			if (this.chooseArr.length > 0) {
				let orderArr = JSON.stringify(this.chooseArr)
				uni.navigateTo({
					url: `../settlement/settlement?orderArr=${orderArr}&allPrice=${this.checkedPrices}`,
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			} else {
				uni.showToast({
					title: '请选择您要购买的商品',
					icon: "none"
				});
			}

		},
		change() {}
	},
	mounted() {

	},
	onShow() {
		if (uni.getStorageSync('openId')) {
			this.openId = uni.getStorageSync('openId')
		}
		if (this.openId !== '') {
			this.getCartsNum(this.openId)
			this.chooseArr = []
		}
	},
	onLoad() {

	},
	filters: {

	},
	computed: {
		// ...mapState(['cartsArr']),
		...mapGetters(['newCartsArr']),
		// 全选框状态
		allFlag() {
			if (this.chooseArr.length == this.newCartsArr.length) {
				return true
			} else {
				return false
			}
		},
		// 选中的商品总件数
		checkedGoods() {
			let num = 0
			this.chooseArr.map(item => {
				if (item.checked) {
					num += item.number
				}
			})
			return num
		},
		// 选中商品的价格
		checkedPrices() {
			let total = 0
			this.chooseArr.map(item => {
				if (item.checked) {
					total += (item.number * item.retail_price)
				}
			})
			return total
		}
	},
	watch: {

	},
	directives: {

	}
}
